import React from 'react';
import react from '@servicenow/ui-renderer-react';

const {createSlot} = react;

const handleEnter = f => e => {
	if (e.key === 'Enter') {
		e.preventDefault();
		f(e);
	}
};

export default function NotHTMLOption(props) {
	const {
		id,
		value,
		onSelect,
		onHighlight,
		children,
		highlighted,
		selected,
		showSeparator,
		accessoryLabel,
		disabled,
		isSelected,
		role
	} = props;


	const contentClassName = `sn-popover--item--content ${isSelected ? 'is-option-selected' : highlighted && !disabled ? 'is-hover' : ''}`;
	// override the :hover state style
	const style = (highlighted && !disabled) || isSelected ? undefined : { background: 'initial' };
	const separator = (showSeparator) ? <div className="sn-popover--separator" /> : null;
	const accessoryEl = (accessoryLabel) ? <span className="sn-popover--item--accessory">{accessoryLabel}</span> : null;
	const content = <div style={style} className={contentClassName}><span className="sn-popover--item--title">{children}</span>{accessoryEl}</div>;
	return (
		<React.Fragment>
			{separator}
			<li
				id={id}
				tabIndex="-1"
				value={value}
				className={`sn-popover--item ${selected ? 'mod-select is-selected' : ''}`}
				role={role}
				onClick={onSelect}
				onKeyDown={handleEnter(onSelect)}
				onMouseEnter={onHighlight}
				onFocus={onHighlight}
				aria-label={value || undefined}
				aria-selected={selected || undefined}
			>
				{createSlot(content) /* Wrap children in span to ellipse text */}
			</li>
		</React.Fragment>
	);
}
