import React from 'react';
import react from '@servicenow/ui-renderer-react';

import partition from 'lodash/partition';
import unzip from 'lodash/unzip';
import noop from 'lodash/noop';
import isFunction from 'lodash/isFunction';
import DropdownContainer from './DropdownContainer';
import PropTypes from 'prop-types';
import style from './_dropdown.scss';

const {createSlot} = react;

class Dropdown extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			show: false,
			highlighted: 0
		};
		this.onSelect = this.onSelect.bind(this);
		this.toggle = this.toggle.bind(this);
		this.onShow = this.onShow.bind(this);
		this.onHide = this.onHide.bind(this);
		this.onKeyDown = this.onKeyDown.bind(this);
		this.onOptionHighlight = this.onOptionHighlight.bind(this);
	}

	componentDidMount() {
		if (this.props.showOnRender === true)
			this.toggle();
	}

	containerRefSetter = (ref) => {
		this.containerRef = ref;
	};

	selectDivElement = (ref) => {
		this.divElement = ref;
		this.props.refSelectContainer(ref);
	};

	onOptionHighlight(index, originalFn) {
		return e => {
			isFunction(originalFn) && originalFn(e);
			this.setState({ highlighted: index });
		};
	}

	componentWillReceiveProps({ children }) {
		// when there are now less children, reset back to 0
		if(React.Children.count(children) < React.Children.count(this.props.children)) {
			this.setState({ highlighted: 0 });
		}
	}

	render() {
		const {id, children, style, align, gapHeight, verticalLock} = this.props;

		const containerId = `${id}-dropdown-container`;

		const [select, options] = partition(React.Children.toArray(children), c => c.props.slot === 'select');

		const [ modifiedOptions=[], optionIds=[] ] = unzip(options.map((c, i) => {
			const id = `${containerId}-${i}`;
			return [
				React.cloneElement(c, {
					id,
					onHighlight: this.onOptionHighlight(i, c.props.onHighlight),
					highlighted: this.state.highlighted === i,
					onSelect: () => this.onSelect(i)
				}),
				id
			];
		}));

		return (
			<div
				className={`dropdown-field ${this.props.multiSelect ? 'multiselect' : ''}`}
				style={style}
				onKeyDown={this.onKeyDown}
			>
				{this.renderControl(id, containerId, select)}

				<DropdownContainer
					parentId={id}
					containerId={containerId}
					optionIds={optionIds}
					align={align}
					verticalLock={verticalLock}
					gapHeight={gapHeight}
					onSelect={this.onSelect}
					positionFixed={this.props.positionFixed}
					hasScroll={this.props.hasScroll}
					onScrollBottom={this.props.onScrollBottom}
					closeOnClick={!this.props.multiSelect}
					anchorComponent={this.divElement}
					onShow={this.onShow}
					onHide={this.onHide}
					show={this.state.show}
					highlighted={this.state.highlighted}
					multiSelect={this.props.multiSelect}
					onKeyDown={this.onKeyDown}
					containerRef={this.containerRefSetter}
				>
					{modifiedOptions}
				</DropdownContainer>
				{this.state.show && this.props.createAccessibilityMessage && <span aria-atomic aria-live="polite" className="snu-sr">
					{this.props.createAccessibilityMessage(this.state.highlighted)}
				</span>}
			</div>
		);
	}

	renderControl(id, containerId, select) {
		const props = {
			id: id,
			onClick: this.toggle,
			'aria-label': this.props.ariaLabel,
			role: 'button',
			'aria-haspopup': this.props.multiSelect ? 'listbox' : 'menu',
			'aria-expanded': this.state.show,
			'aria-controls': this.state.show ? containerId : undefined,
		};
		if (this.props.dontWrapSlottedSelect) {

			return <div ref={this.selectDivElement}>
				{select
					.map((e) => React.cloneElement(e, {
						...props,
					}))}
			</div>;
		}

		return <div
			tabIndex="0"
			ref={this.selectDivElement}
			{...props}
		>
			{createSlot(select, 'select')}
		</div>;
	}

	onShow() {
		this.setState({ highlighted: this.props.firstHighlightedOption });

		this._setShow(true);

		this.props.onShow();
	}

	onKeyDown(e) {
		const {cycle} = this.props;

		if(e.defaultPrevented) {
			return;
		}

		if(e.key === 'Escape') {
			e.preventDefault();
			this.onHide(true);
		}

		if(e.key === 'Enter') {
			e.preventDefault();
			if(this.state.show) {
				this.onSelect();
			} else {
				this._setShow(true);
			}
		}

		if(e.key === 'Tab') {
			// don't prevent the event default behavior so the browser can handle tabbing
			this.onHide(true);
		}

		if(this.state.show) {
			switch (e.key) {
			case 'ArrowUp': {
				e.preventDefault();
				const length = React.Children.toArray(this.props.children)
					.filter(c => c.props.slot === 'options').length;

				// don't do anything when there's no options
				if (length < 1) {
					return;
				}

				const beforeFirst = cycle ? length-1 : 0;
				const newHighlighted = this.state.highlighted === 0 ? beforeFirst : this.state.highlighted - 1;
				this.setHighlighted(newHighlighted);

				break;
			}
			case 'ArrowDown': {
				e.preventDefault();
				const length = React.Children.toArray(this.props.children)
					.filter(c => c.props.slot === 'options').length;

				// don't do anything when there's no options
				if(length < 1) {
					return;
				}

				const beyondLast = cycle ? 0 : this.state.highlighted;
				const newHighlighted = this.state.highlighted === length-1 ? beyondLast : this.state.highlighted + 1;
				this.setHighlighted(newHighlighted);

				break;
			}
			}
		}
	}

	setHighlighted(newHighlighted) {
		if (this.state.highlighted !== newHighlighted) {
			this.props.onActiveChange(newHighlighted);
		}

		this.setState({
			highlighted: newHighlighted
		});

	}

	onSelect(highlighted = this.state.highlighted) {
		const { scrollTop } = this.containerRef || {};
		this.props.onSelect(highlighted);
		if (!this.props.multiSelect) {
			this.toggle();
		} else {
			// the consumer may manually focus on another element in `this.props.onSelect`
			// need to maintain scroll position for multi-selectable dropdowns
			this.containerRef && (this.containerRef.scrollTop = scrollTop);
		}
	}

	onHide(refocus = false) {
		this._setShow(false);
		if(refocus) {
			// refocus to handle the portal tab ordering :(
			this.divElement && this.divElement.focus();
		}

		this.props.onHide();
	}

	_setShow(show) {
		this.setState({ show });
	}

	toggle() {
		this._setShow(!this.state.show);
	}
}


Dropdown.propTypes = {
	/**
	 * Inline style
	 */
	style: PropTypes.object,

	/**
	 * Gap height (in pixels), between the dropdown and the control button. Default is 5
	 */
	gapHeight: PropTypes.number,

	/**
	 * Base id used for generating option ids. Used for aria/screenreaders
	 */
	id: PropTypes.string,

	/**
	 * Whether the options are multi-selectable
	 */
	multiSelect: PropTypes.bool,

	/**
	 * Set the default alignment for the dropdown - note that this can conditionally change
	 * with edge collision
	 */
	align: PropTypes.string,

	/**
	 * Set to cycle options when using up/down arrow keys
	 */
	cycle: PropTypes.bool,

	/**
	 * Keeps the dropdown from vertically flipping on collisions
	 */
	verticalLock: PropTypes.bool,

	/**
	 	* Dropdown renders expanded on initial render
	 */
	showOnRender: PropTypes.bool,

	/**
	 * Whether the content in the dropdown container needs to scroll
	 */
	hasScroll: PropTypes.bool,
	/**
	 * This gives the dropdown fixed positioning to get past any overflow: hidden containers
	 * Only use if you can't ensure that the dropdown width will fit in your parent container.
	 * The dropdown will disappear when you scroll past the anchor point (to avoid clipping issues)
	 */
	positionFixed: PropTypes.bool,
	/**
	 * Label to apply to control button. Needed for some screen readers that speak only the label
	 * attribute on a button itself and not the label of the children.
	 */
	ariaLabel: PropTypes.string,

	/**
	 * Called with the index of the option that has been selected
	 */
	onSelect: PropTypes.func,
	/**
	 * Called with the index of the option that has been highlighted (but not selected)
	 */
	onActiveChange: PropTypes.func,
	/**
	 * Called when the dropdown is triggered to show
	 */
	onShow: PropTypes.func,
	/**
	 * Called when the user has scrolled to the last option in the dropdown
	 */
	onScrollBottom: PropTypes.func,
	/**
	 * Called to create an accessibility message for the currently highlighted option.
	 *
	 * This is a workaround for the case where this component is used in a shadow-dom context
	 */
	createAccessibilityMessage: PropTypes.func,
	/**
	 * First highlighted option when the dropdown expands
	 */
	firstHighlightedOption: PropTypes.number,
	/**
	 * This is providing a ref for eds-tooltip
	 */
	refSelectContainer: PropTypes.func
};

Dropdown.defaultProps = {
	gapHeight: 5,
	multiSelect: false,
	align: 'left',
	cycle: false,
	verticalLock: false,
	hasScroll: false,
	positionFixed: false,
	firstHighlightedOption: 0,
	showOnRender: false,

	onSelect: noop,
	onActiveChange: noop,
	onShow: noop,
	onHide: noop,
	onScrollBottom: noop,
	refSelectContainer: noop
};

Dropdown.style = style;
export default Dropdown;
