import react from '@servicenow/ui-renderer-react';
import React from 'react';
import noop from 'lodash/noop';
import PropTypes from 'prop-types';
import {addClickListener, isElementInPath} from 'sn-seismic-addons';

class Portal extends React.Component {
	constructor(props) {
		super(props);
		this._onClick = this._onClick.bind(this);

		if (props.show) this._show();
	}

	componentWillReceiveProps(nextProps) {
		const {show} = nextProps;
		if (this.props.show !== show) {
			show ? this._show() : this._hide();
		}
	}

	_portalNodeRefSetter = (node) => {this._portalNode = node;};

	_show() {
		// setTimeout gets us beyond the click that may have cause the show in the first place
		setTimeout(() => this.removeClickListenerFn = addClickListener(this._onClick));
		this.props.onShow();
	}

	_hide() {
		this.removeClickListenerFn();
		this.props.onHide();
	}

	_onClick(event) {
		if (event.event.button !== 0) {
			return;
		}

		if (this.props.closeOnClick || !isElementInPath(this._portalNode, event.path)) {
			this._hide();
		}
	}

	render() {
		if (!this.props.show) {
			return null;
		}

		const {id, className, style, children} = this.props;

		return (
			<div
				id={id}
				className={className}
				style={{
					...style,
					position: this.props.positionFixed ? 'fixed' : 'absolute'
				}}
				ref={this._portalNodeRefSetter}>

				{children}

			</div>
		);
	}
}

Portal.propTypes = {
	id: PropTypes.string,
	className: PropTypes.string,

	style: PropTypes.shape({
		top: PropTypes.string,
		left: PropTypes.string,
		minWidth: PropTypes.string,
	}),

	closeOnClick: PropTypes.bool,
	show: PropTypes.bool,

	onShow: PropTypes.func,
	onHide: PropTypes.func
};

Portal.defaultProps = {
	onShow: noop,
	onHide: noop,
};

export default Portal;
