import react from '@servicenow/ui-renderer-react';
import React from 'react';

import Portal from './Portal';
import noop from 'lodash/noop';
import get from 'lodash/get';
import PropTypes from 'prop-types';

const SCROLL_VALUES = /(auto|scroll)/;

class DropdownContainer extends React.Component {
	constructor(props) {
		super(props);
		this.state = {};
		this.portalId = `portal-${this.props.containerId}`;
		this.onScroll = this.onScroll.bind(this);
		this._onAnimationFrame = this._onAnimationFrame.bind(this);
		this._positionCheck = this._positionCheck.bind(this);
	}

	componentWillReceiveProps(nextProps) {
		if (nextProps.show) {
			if (!this.props.show) {
				window.requestAnimationFrame(this.props.positionFixed ? this._positionFixedCheck : this._positionCheck);
			}
		} else {
			this.setState({location:undefined});
		}
	}

	componentDidUpdate({ highlighted, children, hasScroll }) {
		if (hasScroll && this.dropdownList && this.props.highlighted !== highlighted) {
			const total = React.Children.count(children);
			const numberOfOptions = React.Children.toArray(children)
				.filter(c => c.props.slot === 'options')
				.length;

			const containerHeight = this.dropdownList.getBoundingClientRect().height;
			const containerTop = this.dropdownList.scrollTop;
			const containerBottom = containerTop + containerHeight;

			if (highlighted <= (total - numberOfOptions)) {
				// go to top in case of any items preceding real options
				this.dropdownList.scrollTop = 0;
			} else {
				let activeItem = this.dropdownList.querySelector('.sn-popover--item--content.is-hover');
				if (activeItem) {
					while (activeItem.offsetParent && activeItem.offsetParent.id !== this.portalId) {
						activeItem = activeItem.offsetParent;
					}
					const activeItemTop = activeItem.offsetTop;
					const activeItemBottom = activeItemTop + activeItem.offsetHeight;

					if (activeItemTop < containerTop) {
						this.dropdownList.scrollTop = activeItemTop;
					} else if (activeItemBottom > containerBottom) {
						this.dropdownList.scrollTop = activeItemBottom - containerHeight;
					}
				}
			}
		}
	}

	portalRefSetter = (ref) => {
		this._portal = ref;
	}

	dropdownRefSetter = (ref) => {
		const {containerRef} = this.props;
		if (containerRef) {
			containerRef(ref);
		}
		this.dropdownList = ref;
	}

	childrenWithRole = () => {
		const role = this.props.multiSelect ? 'option' : 'menuitem';
		return React.Children.map(this.props.children, e => React.cloneElement(e, {role: role}));
	}

	render() {
		const {location} = this.state;
		const {
			containerId,
			parentId,
			hasScroll,
			multiSelect,
			highlighted,
			optionIds
		} = this.props;

		const highlightedId = get(optionIds, highlighted);
		return (
			<Portal
				id={this.portalId}
				className='sn-popover sn-popover__fixed'
				style={location}
				{...this.props}>
				<div className="sn-popover--body"
					ref={this.portalRefSetter}
					onKeyDown={this.props.onKeyDown}
					aria-activedescendant={highlightedId}
				>
					<div
						id={containerId}
						tabIndex="-1"
						className="sn-popover--body--content"
						onScroll={this.onScroll}>
						<ul
							ref={this.dropdownRefSetter}
							id="dropdownList"
							style={hasScroll ? { overflowY: 'scroll' } : {}}
							className="sn-popover--list"
							aria-multiselectable={multiSelect}
							aria-controls={parentId}
							role={multiSelect ? "listbox" : "menu"}
						>
							{this.childrenWithRole()}
						</ul>
					</div>
				</div>
			</Portal>
		);
	}

	_positionFixedCheck = () => {
		const {show, anchorComponent} = this.props;
		if (!show) {
			return;
		}

		window.requestAnimationFrame(this._positionFixedCheck);

		if (!anchorComponent || !this._portal) {
			return;
		}

		const {bottom, right, width, height} = anchorComponent.getBoundingClientRect();
		const offset = Math.max(width, this._portal.getBoundingClientRect().width);
		const newLocation = {
			top: `${bottom + this.props.gapHeight}px`,
			left: `${right - offset}px`,
			minWidth: `${offset}px`
		};

		const {location} = this.state;
		if (!location ||
			(newLocation.top !== location.top)) {
			this.setState({location : newLocation});
		}

		const scrollParent = this._getScrollParent();
		const top = scrollParent ? scrollParent.getBoundingClientRect().top : 0;
		if (top >= bottom) {
			this.props.onHide();
		}
	};

	_positionCheck() {
		const {show, anchorComponent, gapHeight, align, verticalLock} = this.props;

		if ( !(show && anchorComponent && this._portal) ) {
			return;
		}

		const {height: anchorHeight, width: anchorWidth} = anchorComponent.getBoundingClientRect();
		const scrollParent = this._getScrollParent();
		const {
			height: pageHeight, width: pageWidth, top: pageScrollOffset, x: pageLeft
		} = scrollParent
			? scrollParent.getBoundingClientRect()
			: {height:  window.innerHeight, width: window.innerWidth, top: 0, x: 0};
		const {
			height: portalHeight, bottom: portalBottom, width: portalWidth, x: portalLeft
		} = this._portal.getBoundingClientRect();
		const containerBottom = portalBottom + gapHeight - pageScrollOffset;
		const bottomHit = pageHeight < containerBottom;
		const portalLeftOffset = portalLeft - pageLeft;
		const pageRight = pageLeft + pageWidth;
		const leftHit = portalWidth > portalLeftOffset;
		const rightHit = portalWidth + portalLeft > pageRight;
		const doVerticalFlip = bottomHit && !verticalLock;

		let newLocation = {
			minWidth: Math.max(anchorWidth, portalWidth) + 'px',
			top: (doVerticalFlip ? -(portalHeight + gapHeight) : anchorHeight + gapHeight) + 'px'
		};

		if ( (align === 'left' && rightHit) || (align === 'right' && !leftHit) ) {
			newLocation.right = '0px'; // right align - unstyled is left align
		}

		const {location} = this.state;
		if (!location ||
			(newLocation.top !== location.top)) {
			this.setState({location : newLocation});
		}
	}

	_getScrollParent() {
		if (this._scrollParent) {
			return this._scrollParent;
		}

		let node = this.props.anchorComponent.parentNode;

		while (node) {
			if (node instanceof HTMLElement) {
				const {overflowX, overflowY} = window.getComputedStyle(node);
				const isScrollable = SCROLL_VALUES.test(overflowY) || SCROLL_VALUES.test(overflowX);

				if (isScrollable) {
					return this._scrollParent = node;
				}
			}

			node = node.parentNode || node.host;
		}

		// No scrollParent exists, just return root
		return document.body;
	}

	onScroll(event) {
		if (!this.props.onScrollBottom) {
			return;
		}

		this._scrollTargetElement = event.target;
		window.requestAnimationFrame(this._onAnimationFrame);
	}

	_onAnimationFrame() {
		const { scrollHeight, scrollTop, clientHeight } = this._scrollTargetElement;
		const hasReachedBottom = (clientHeight === scrollHeight - scrollTop);
		if (hasReachedBottom) {
			this.props.onScrollBottom();
		}
	}
}

DropdownContainer.propTypes = {
	parentId: PropTypes.string,
	containerId: PropTypes.string,
	align: PropTypes.string,
	verticalLock: PropTypes.bool,
	gapHeight: PropTypes.number,

	highlighted: PropTypes.number,

	multiSelect: PropTypes.bool,
	hasScroll: PropTypes.bool,
	closeOnClick: PropTypes.bool,
	show: PropTypes.bool,

	onSelect: PropTypes.func,
	onShow: PropTypes.func,
	onHide: PropTypes.func,
	onScrollBottom: PropTypes.func,
	onKeyDown: PropTypes.func,
	containerRef: PropTypes.func,

	anchorComponent: PropTypes.any
};

DropdownContainer.defaultProps = {
	onSelect: noop,
	onShow: noop,
	onHide: noop,
	onKeyDown: noop,
	containerRef: noop,
	align: 'left',
	verticalLock: false
};

export default DropdownContainer;
